//PATHS
@fonts-path: "../fonts/";
@images-path:"../img/";

//FONTS
@font-name: "Raleway";
@font-fallback: "Roboto", Arial, sans-serif;
@font-family-regular: "Raleway-Regular";
@font-family-bold: "Raleway-Bold";
@font-family-black: "Raleway-Black";

@font-body-name: "Roboto";
@font-body-fallback: "Helvetica Neue", Helvetica, Arial, sans-serif;
@font-body-family-light: "Roboto-Light";
@font-body-family-regular: "Roboto-Regular";
@font-body-family-medium: "Roboto-Medium";
@font-body-family-bold: "Roboto-Bold";
@font-body-family-black: "Roboto-Black";
//override
@headings-font-family: @font-name, @font-fallback;
@font-family-base: @font-body-name, @font-body-fallback;
//FONT SIZE
@font-size-xsmall: 14px;
@font-size-small: 16px;
@font-size-normal: 20px;
@font-size-medium: 25px;
@font-size-large: 32px;
@font-size-xlarge: 48px;
@font-size-xxlarge: 57px;

//DIMENSIONS
@navbar-height: 70px;
@logo-width: 188px;
@section-logo-width-xs: 140px;
@section-logo-height-xs: 95px;
@logo-top: 13px;
@section-padding: 70px;
@section-padding-xs: 20px;
@footer-padding: 50px;
@section-primary-whitespace: 15%;
@map-height: 400px;
@blog-thumb-height: 240px;
@heading-image-margin: 30px;
@jumbotron-line-length: @screen-tablet - 2*15px;
@jumbotron-line-length-long: 850px;
@jumbotron-primary-min-height: 590px;
@jumbotron-default-min-height: 240px;
@partners-logo-width: 180px;
@partners-logo-height: 130px;
@static-width: 800px;

@img-width-xs: 200px;
@mobile-min-width:320px;
@mobile-min-height:480px;

//PERCENTAGES
@blog-ratio: 86%;

//override NAVBAR
@grid-float-breakpoint: 1180px;
@grid-float-breakpoint-max: 1179px;


