textarea {
    resize: vertical;
}


.form-control {
    background-color: transparent;
    border:none;
    font-size: @font-size-normal;
    font-weight: 300;
    font-family:inherit;
    .borderRadius();
    
    .box-shadow(0 1px 0 @input-border);
    &:focus{
            outline: 0;
        .box-shadow(0 2px 0px @brand-primary);
        color:@brand-primary;
    }
    &[disabled],
    &[readonly],
        {
            opacity: .7;
        }
    }

form {
    .form-group {
        margin-bottom: 0;
        &.responses{
            height:40px;
        }
    }
}