.fontFace('@{font-name}', '@{font-family-regular}', 400);
.fontFace('@{font-name}', '@{font-family-bold}', 700);
.fontFace('@{font-name}', '@{font-family-black}', 900);

.fontFace('@{font-body-name}', '@{font-body-family-light}', 300);
.fontFace('@{font-body-name}', '@{font-body-family-regular}', 400);
.fontFace('@{font-body-name}', '@{font-body-family-medium}', 500);
.fontFace('@{font-body-name}', '@{font-body-family-bold}', 700);
.fontFace('@{font-body-name}', '@{font-body-family-black}', 900);
p {
    font-size: @font-size-normal;
}
h1, h2, h3{
    font-weight: 900;
    span {
        color:@brand-primary;
    }
}
.btn, a, p {
    font-family: @font-body-name, @font-body-fallback;
}
.btn {font-weight: 700;}
.btn-lg{
    font-weight:900;
    font-size: @font-size-normal;
}

.jumbotron {
    h1 {font-weight: 700;}
    &.primary{
        h1{font-weight: 900;}
        h1, p {text-align: left;}
    }
    &.default {
        h1 {font-size: @font-size-xlarge;}
    } 
}

.section {
    
    h1, h2 {
        text-transform: uppercase;
        margin-bottom: 40px;
    }

    .thumbnail {

        p {
            font-size: @font-size-small;
        }
    }
    &.inverse{        
         p { font-weight: 400; }
         h1, h2, h3{
            span {
                color:#fff;
            }
        }
    }
    &.section-button {
        p {
            font-family: @font-name;
            font-size: @font-size-normal;
        }
        .btn{margin-top:1em;}
    }
    &.section-primary, &.section-list {
        p{
            font-weight: 400;
        }        
        h1 {
            font-size: @font-size-xxlarge;
            font-weight: 900;
            margin-bottom: 10px;
        }
        .btn {margin-top: 10px}
    }
    &.section-list,
    &.section-primary.list {
        p, ul, ol, a {
            font-size: @font-size-small;
        }
        p, ul, ol {font-weight: 400;}
        h1{
            margin-bottom:15px;
        }
        p, ul, ol, h3 {text-align: left;}
        .btn {text-align: center;}
        h3{
            font-size: @font-size-small;
            font-weight: 900;
            text-transform: uppercase;
        }
        ul{ 
            .list-unstyled;
            li{
                &:before{
                    content:"\2022 ";
                    padding-right: 5px;
                }
            }
        }
        ol{
            list-style-position: inside;
            padding: 0;
        }
        .odd {
            h1 {
                font-size: @font-size-xlarge;
                font-weight: 900;
            }
        }        
    }
    &.section-thumbnails{
        .thumbnail{
            .caption{
                h1, h2, h3 {
                    font-weight: 700;
                }
            }
        }
    }
    &.section-blog {
        .thumbnail {
            .caption{
                height: @blog-thumb-height;
                h1{
                    font-weight: 900;
                    font-size: @font-size-medium;
                    text-transform: uppercase;
                    margin-bottom: 18px;
                }
                p{
                    font-size: @font-size-normal;
                }
                a {
                    font-weight: 700;
                    font-size: @font-size-small;
                    text-decoration: underline;
                }
            }
        }
    }
}
@media (max-width: calc(@grid-float-breakpoint -1)) {
    
    .navbar-nav>li>a {
        padding-top: 12px;
        padding-bottom: 12px;
        font-size: 1.1em;
        font-weight: 300;
    }
}

section.static {
    h2 {text-transform: uppercase;}
    p{
    font-size: @font-size-small;
    }
    ul, ol {
        padding: 0;
        font-size: @font-size-small;
        p{
        font-size: @font-size-small;
        }
    }
    ul {
        list-style: none;
    }
    ol{
        &>li{
            font-weight: 900;
            ul, ol, li {
                font-weight: 400;
                ul {
                    list-style-type: square;
                    li {
                        font-weight: 700;
                        }
                }
                ul, ol {
                    padding-left: 18px;
                    margin-bottom: 15px;
                }
            }
            ul:first-child, ol:first-child {
                margin-top: 10px;
            }
            ul:last-child, ol:last-child {
                margin-bottom: 10px;
            }
        }
    }
}

@media (max-width: @screen-sm) {

    .btn-lg{
        font-size: 1.2em;
    }

    .jumbotron.primary h1 {
        font-size: 2.8em;
        line-height: 1;
    }
    .jumbotron.default h1 {
        font-size: 2.6em;
    }
    p, a, span, .thumbnail p{
        font-size: 1em;
    }
    .section.section-button p,
    .section.section-blog .thumbnail .caption p,
    .jumbotron p,{
        font-size: 1em;
        line-height: 1.2;
    }

    .section.section-list h1,
    .section.section-primary h1,
    .section.section-list .odd h1,
    .section.section-primary.list .odd h1 {
        font-size: 1.8em;
    }
    .section.section-list .odd .logo + h1,
    .section.section-primary.list .odd .logo + h1{
        font-size: 1.4em;
    }
     .section.section-primary.list {
        p, ul, ol, a {
            font-size: 1em;
        }
    }
    .section.section-list h3 {margin-bottom: 0;}
    .section.section-blog .thumbnail .caption {height: auto;}

    .form-group {
        font-size: 1.2em;
        label {font-size: 0.9em}
    }
}

