.fontFace(@aFontName, @aFontFile, @aFontWeight: normal, @aFontStyle: normal) {
	@font-face {
		font-family: @aFontName;
		src: url('@{fonts-path}@{aFontName}/@{aFontFile}.eot');
		src: url('@{fonts-path}@{aFontName}/@{aFontFile}.eot?#iefix') format('embedded-opentype'),
			 url('@{fonts-path}@{aFontName}/@{aFontFile}.woff') format('woff'),
			 url('@{fonts-path}@{aFontName}/@{aFontFile}.ttf') format('truetype'),
			 url('@{fonts-path}@{aFontName}/@{aFontFile}.svg#@{aFontName}') format('svg');
		font-weight: @aFontWeight;
		font-style: @aFontStyle;
	}
}


.bgFixed(@size: cover; @pos:center top){
	background-size: @size;
	background-repeat: no-repeat;
	background-position: @pos;
	@media(min-width:1025px) {
		background-attachment: fixed;
	}
}

.absCenter(){
	position: absolute;
	top:0;
	right:0;
	bottom:0;
	left:0;
}

.fixedCenter(){
	position: fixed;
	top:0;
	right:0;
	bottom:0;
	left:0;
}

.absCenterTranslate(@width, @height){
	position: absolute;
	top:50%;
	left:50%;
	margin-top:-@height/2;
	margin-left:-@width/2;
}

.borderRadius (@radius: 0) {
	-webkit-border-radius: @radius;
	-moz-border-radius:    @radius;
	border-radius:         @radius;

	-moz-background-clip:    padding;
	-webkit-background-clip: padding-box;
	background-clip:         padding-box;
}

.fixWhitespace(@padding: @section-padding, @padding-xs: @section-padding-xs){
	& + &,
	&.inverse + &.inverse {
		padding-top:0;
	}
	& + &.inverse,
	&.inverse + & {
		padding-top: @padding;
		@media (max-width: @screen-sm) {
			padding-top: @padding-xs;
		}
	}  
}

.boxShadow(@args: 0px 32px 32px 0px rgba(0,0,0,0.1)){
	-webkit-box-shadow: @args;
-moz-box-shadow: @args;
box-shadow: @args;
}

.transition (@property:opacity, @duration:.5s, @easing:cubic-bezier(0.42,0,0.58,1), @delay:0s) {
	-webkit-transition: @property @duration @easing @delay;  
	-moz-transition:    @property @duration @easing @delay;
	-ms-transition:     @property @duration @easing @delay; 
	-o-transition:      @property @duration @easing @delay;  
}

// use this if you wish to also toggle visibility with delay
.transitionHide (@property: opacity, @duration:0.6s, @easing:linear, @delay:2.4s) {
	@time: @delay + @duration;
	-webkit-transition: @property @duration @easing @delay, visibility @duration linear @time;  
	-moz-transition:    @property @duration @easing @delay, visibility @duration linear @time; 
	-ms-transition:     @property @duration @easing @delay, visibility @duration linear @time; 
	-o-transition:      @property @duration @easing @delay, visibility @duration linear @time;  
}

.transform(@string){
	-webkit-transform: @string;
	-moz-transform: 	 @string;
	-ms-transform: 		 @string;
	-o-transform: 		 @string;
}

.containing-table {
    display: table;
    width: 100%;
}
.center-align {
    display: table-cell;
    text-align: center;
    vertical-align: middle;
}
.center-content {
    display: inline-block;
    vertical-align: top;
}