.section{
    color: @gray-base;
    background-color:transparent;
    padding-top:@section-padding;
    padding-bottom:@section-padding;
    @media (max-width: @screen-sm) {
        padding-top:@section-padding-xs;
        padding-bottom:@section-padding-xs;
        .btn {
            margin-top:10px;
        }
    }
    p:first-child{margin-top:15px}    
    &.section-default{
        text-align: center;
        background-color:#fff;
    }

    &.section-primary{
        background-color:#fff;
        .fixWhitespace();
            @media (min-width: @screen-sm){
                .caption{
                    padding-top:@section-padding/2;
                }
             }
        .img-responsive{
            margin:20px auto;
            @media (min-width: @screen-sm) {
                padding-left: 0;
                padding-right: @section-primary-whitespace
            }
        }
        .odd{
            .make-md-column(6);
            .make-sm-column(6);
        }
        .even{
            .make-md-column(6);
            .make-sm-column(6);
        }
        &.reverse{
            .odd{
                .make-md-column-push(6);
                .make-sm-column-push(6);
            }
            .even{
                .make-md-column-pull(6);
                .make-sm-column-pull(6);
            }
            .img-responsive{
                @media (min-width: @screen-sm) {
                    padding-left: @section-primary-whitespace;
                    padding-right: 0
                }
            }
        }
        &.snap-down{
            .img-responsive{margin-bottom:-@section-padding;}
        }
        &.snap-up{
            .img-responsive{margin-top:-@section-padding;}
        }
        @media (max-width: @screen-sm) {
            &.snap-down, &.snap-up{
                .img-responsive{margin:calc(@section-padding - 15px) auto;}
            }
        }
    }
    &.section-list {
        .logo, .logo img {
            margin:0;
            margin-top:@heading-image-margin;
            @media (max-width: @screen-sm) {
                margin:auto;
                max-width: @section-logo-width-xs;
                max-height: @section-logo-height-xs;
            }
        }
        .img-responsive{
            margin:auto;
            margin-top:@heading-image-margin;
            @media (min-width: @screen-sm) {
                padding-left: 0;
                padding-right: @section-primary-whitespace   
            }         
        }
        .odd{
            .make-md-column(5);
            .make-sm-column(5);
        }
        .even{
            .make-md-column(7);
            .make-sm-column(7);
        }
        &.reverse{
            .odd{
                .make-md-column-push(7);
                .make-sm-column-push(7);
            }
            .even{
                .make-md-column-pull(5);
                .make-sm-column-pull(5);
            }
            .img-responsive{
                @media (min-width: @screen-sm) {
                    padding-left: @section-primary-whitespace;
                    padding-right: 0
                }
            }
        }
        &.inverse {
            p strong {font-weight: 900}
        }
        .fixWhitespace();       
    }

    &.section-button{
        text-align: center;
        .btn-primary{
            margin-top: 10px;
        }
    }

    &.section-thumbnails{
        .thumbnail {
            h3 {
                min-height: 56px;
            }
        }        
    }

    &.section-blog {
        .thumbnail {
            background:#fff;
            padding:0;
            .image{
                position: relative;
                width:100%;
                padding-bottom:@blog-ratio;
                overflow: hidden;
                img{
                    max-height: 120%;
                    margin:auto;
                    .absCenter();
                }
            }
            .caption{
                padding:15px;
            }
        }
    }

    &.section-form{
        text-align: center;
        .container {
            max-width: 800px;
        }
        
        form {
            max-width: 400px;
            margin: auto;

            label {
                opacity: 0;
            }

            .btn {
                margin-top: 15px;
            }
        }          
        .map {
            height: @map-height;
            background: #ededed;
        }
         @media (max-width: @screen-sm){
             &{padding:15px;}
         }
    }
    &.inverse{
        background-color: @brand-primary;
        color:#fff;
        .btn-primary{
            color: @btn-default-color;
            background-color: @btn-default-bg;
            border-color: @btn-default-border;
        }
    }
}

@media (max-width: @screen-sm){
    .map>div {
        height: 250px!important;
        margin-top: 40px;
    }
}

section.static {
    max-width: @static-width;
    padding-top:3em;
    padding-bottom:6em;
    margin:auto;    
}

.footer {
    font-size: @font-size-xsmall;
